import React from 'react'

import styles from "./Button.sass"

const Button = ({
    text
}) => {
    return (
        <a
            className="btn-custom"
            href='mailto:dan.sendbot@gmail.com'
        >
            { text }
        </a>
    )
}

Button.defaultProps = {
    text: "Default button text"
}

export default Button
