import React from "react"

// Import modules
import AutoTyper from "../AutoTyper/AutoTyper"

// Import data
import personal_attributes from "../../assets/json/personal_attributes.json"

// Import images
import bannerImage from '../../assets/images/banner.jpg'

// Import css
import style from './Banner.css'

// Styles
const image_container_style = {
    backgroundImage: `url(${bannerImage})`
}

const Banner = () => {
    return (
        <div id="Banner">
            <div
                id="image-container"
                className="flex"
                style={image_container_style}
            >
                <h1 className="item">Hi there, my name is Dan, and I am a:</h1>
                <AutoTyper className="item" strings={personal_attributes}/>
            </div>
        </div>
    )
}

export default Banner
