import * as React from "react"

// Import components
import Banner from "../components/Banner/Banner"
import Section from "../components/Section/Section"
import Language from "../components/Language/Language"
import Card from "../components/Card/Card"
import Footer from "../components/Footer/Footer"

// Import data
import languages from "../assets/json/languages.json"
import technologies from "../assets/json/technologies.json"

const group = (items, size) => {
  let copy_items = JSON.parse(JSON.stringify(items))
  let n = Math.floor(items.length / size)
  let result = []
  for (let i=0; i < n; i++) {
    let temp = []
    for (let j=0; j < size; j++) {
      temp.push(copy_items.pop())
    }
    result.push(temp)
  }
  // Push remaining if any
  if (copy_items) {
    let temp = []
    for (let i=0; i < copy_items.length; i++) {
      temp.push(copy_items.pop())
    }
    result.push(temp)
  }
  return result
}

const IndexPage = () => {
  return (
    <main>
      <Banner />

      {/* Intro and about me */}
      <Section
        title="A little about me"
        highlight={true}
        description="BSc. in Computing Science. I specialize in Unix-based platforms, full-stack and command-line development, high volume data analysis, and software engineering."
      />

      {/* Languages */}
      <Section
        title="Languages"
        description="I can speak a few languages"
        borderSide="Right"
        alignment="right"
      >
        {
          languages.map((language) => 
            <Language
              icon={language.icon}
              name={language.name}
              value={language.value}
              key={language.icon}
            />
          )
        }
        <p style={{marginTop: "var(--margin-top)"}}>And a few more...</p>
      </Section>

      <Section
        title="Technologies"
        description="Just a few of the technologies I've used"
      >
        {
          group(technologies, 4).map((group, index) =>
            <div
              className="flex-grid"
              style={{
                marginTop: "var(--margin-top)"
              }}
              key={index}
            >
              {
                group.map((tech, index) =>
                  <div
                    className="col"
                    key={tech.name}
                    data-aos="fade-up"
                    data-aos-delay={100 * index}
                  >
                    <Card
                      logo={tech.name}
                      title={tech.name}
                    />
                  </div>
                )
              }
            </div>
          )
        }
        <p style={{marginTop: "var(--margin-top)"}}>And <b>a lot</b> more...</p>
      </Section>
      

      {/* Footer */}
      <Footer></Footer>
    </main>
  )
}

export default IndexPage
