import React, { Children } from 'react'
import PropTypes from 'prop-types';

import Aos from 'aos'
import "aos/dist/aos.css";

import style from "./Section.css"

const Section = ({
    children,
    title,
    alignment,
    description,
    highlight
}) => {

    React.useEffect(() => {
        Aos.init()
        Aos.refresh()
    }, [])

    const pickColor = () => {
        let colors = [
            "--rgreen",
            "--rdarkgreen",
            "--rorange",
            "--ryellow",
            "--rpurple",
            "--rblue",
        ]
        return colors[Math.floor(Math.random() * colors.length)]
    }

    const section_style = {
        textAlign: alignment,
    }

    const h1Style = (highlight) => {
        if (highlight) {
            return {
                color: "var(--rpink2)"
            }
        }
    }

    return (
        <div
            id="Section"
            className="card level-1 container"
            data-aos="fade-up"
        >
            <section
                style={section_style}
            >
                <h1
                    style={h1Style(highlight)}
                >{ title }</h1>
                <p>{ description }</p>
            </section>

            <div id="content">
                { children }
            </div>
        </div>
    )
}

Section.defaultProps = {
    title: "Default Title",
    alignment: "left",
    description: "Default description",
    highlight: false
}

Section.propTypes = {
    alignment: PropTypes.oneOf([
        "left",
        "right",
        "center"
    ]),
    highlight: PropTypes.bool
}

export default Section
