import React from 'react'
import PropTypes from 'prop-types';

import styles from "./Card.sass"

function Card({
    title,
    logo
}) {

    const logo_path = require("../../assets/images/logos/"+ logo + ".png").default

    return (
        <div
            id="Card"
        >
            <div
                id="image"
                style={{
                    backgroundImage: `url(${logo_path})`
                }}
            >
            </div>
            <div id="title">
                { title }
            </div>
        </div>
    )
}

Card.defaultProps = {
    title: "",
    logo: "centos"
}

export default Card
