import React from 'react'

// This is the react syntax, I guess Gatsby is different -_-
// import styles from "./Language.module.css"
import * as styles from "./Language.module.sass"

const Language = ({
    icon,
    name,
    value
}) => {
    const block_style = {
        width: value + "%"
    }

    return (
        <div
            id={styles.Language}
            data-aos="fade-right"
        >
            <div id={styles.label}>
                <img
                    src={require(`../../assets/images/icons/${icon}.png`).default}
                    alt={icon}
                ></img>
                <h5>{ name }</h5>
            </div>

            <div id={styles.bar}>
                <div
                    style={block_style}
                    className={styles.block}
                >
                    <h5>{ value }</h5>
                </div>
            </div>
        </div>
    )
}

export default Language
